}

/* Page Hero Image */
.page-hero-image-container {
  position: relative;
  overflow: hidden;
  min-height: 350px;
}
.page-hero-image {
  position: absolute;
  z-index: 1;
  left: -15px;
  right: -15px;
  top: -15px;
  bottom: -15px;
  &.with_blur {
    filter: blur(3.5px);
  }
}
#page-hero-title {
  z-index: 2;
  width: 100%;
  letter-spacing: 8px;
  font-weight: 600;
  &.block_styled {
    background-color: $primary-light;
  }
}

/* Skeleton Loader */
.skeleton-article {
  position: relative;

  .skeleton-article-image {
    background: #eee;
    height: 15rem;
  }
  .skeleton-article-title {
    display: block;
    background: #eee;
    height: 2rem;
    width: 10rem;
  }
  .skeleton-article-subtitle {
    display: block;
    background: #eee;
    height: 0.75rem;
    width: 3rem;
  }
  .skeleton-article-text {
    display: block;
    background: #eee;
    height: 1rem;
    width: 30rem;

    &.less {
      width: 22rem;
    }
    &.least {
      width: 15rem;
    }
  }

  &::before {
    content: '';
    position: absolute;
    background: linear-gradient(
      90deg,
      transparent,
      rgba(255, 255, 255, 0.9),
      transparent
    );
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;

    @keyframes loading {
      0% {
        transform: skewX(-10deg) translateX(-100%);
      }
      100% {
        transform: skewX(-10deg) translateX(200%);
      }
    }
    animation: loading 0.85s infinite;
  }
}

/* Carousel */
#news-carousel-container img {
  max-height: 300px;
}
.carousel-image {
  display: block;
  height: 100%;
  width: 100%;
}
.carousel-slide {
  height: 55vh;
}
#controls-container {
  position: absolute;
  width: calc(100% - 20px);
  left: 10px;
  top: 50%;
  margin-top: -22.5px;
