#autoplay-button[data-action="stop"], #autoplay-button[data-action="start"] {
  opacity: 0.3;
  box-sizing: border-box;
  border-color: transparent transparent transparent #202020;
  background-color: transparent;
  transition: 100ms all ease;
  will-change: border-width;
  cursor: pointer;
  position: absolute;
  right: 10px;
  top: 10px;
  z-index: 2;
  &:hover {
    opacity: 0.8;
  }
}
#autoplay-button[data-action="start"] {
  height: 18px;
  border-style: solid;
  border-width: 9px 0 9px 17px;
}
#autoplay-button[data-action="stop"] {
  height: 20px;
  border-style: double;
  border-width: 0 0 0 17px;
}

