// Donation Boxes
.radio {
  font-size: inherit;
  margin: 0;
  position: absolute;
  right: 20px;
  top: 20px;
}

@supports(-webkit-appearance: none) or (-moz-appearance: none) {
  .radio {
    z-index: 1;
    -webkit-appearance: none;
    -moz-appearance: none;
    background: white;
    border: 2px solid #e2ebf6;
    border-radius: 50%;
    cursor: pointer;
    height: 25px;
    width: 25px;
    outline: none;
    transition: background 0.2s ease-out, border-color 0.2s ease-out;

    &::after {
      border: 2px solid white;
      border-top: 0;
      border-left: 0;
      content: '';
      display: block;
      height: 0.75rem;
      left: 25%;
      position: absolute;
      top: 50%;
      transform: rotate(45deg) translate(-50%, -50%);
      width: 0.375rem;
    }

    &:checked {
      background: $secondary;
      border-color: $secondary;
    }
  }

  .card:hover .radio {
    border-color: white;

    &:checked {
      border-color: $secondary;
    }
  }
}

.card:hover .donar-box {
  border-color: $secondary;
}

.radio:checked ~ .donar-box {
  border-color: $secondary;
}

.radio:focus ~ .donar-box {
  box-shadow: 0 0 0 2px $secondary;
}

/* 404 */
.linear-primary-gradient {
  font-size: 7rem;
  font-weight: 200;
  background-color: $primary-light; /* fallback color if gradients are not supported */
  background-image: -webkit-linear-gradient(130deg, $primary-light, $primary); /* For Chrome 25 and Safari 6, iOS 6.1, Android 4.3 */
  background-image:    -moz-linear-gradient(130deg, $primary-light, $primary); /* For Firefox (3.6 to 15) */
  background-image:      -o-linear-gradient(130deg, $primary-light, $primary); /* For old Opera (11.1 to 12.0) */
  background-image:         linear-gradient(130deg, $primary-light, $primary); /* Standard syntax; must be last */
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}
