  .standalone-donar-layout {
    flex-direction: column;
    min-height: auto;
  }

  .standalone-donar-info {
    order: 2;
  }

  .standalone-donar-form {
    order: 1;
    padding: 1rem;
  }

  .footer-content {
    min-height: auto;
  }

  // On mobile, don't stack tabs - let each tab size naturally to prevent excess whitespace
  #standalone-donation-modal #tab-contents {
    display: block;

