  font-size: 0.9rem;

  &:hover {
    color: #0ea6df;
  }
}

/* Divider with text */
.is-divider-with-text {
  display: flex;
  align-items: center;
  text-align: center;
  margin: 1rem 0;
  color: #7a7a7a;
  font-size: 0.875rem;

  &::before,
  &::after {
    content: '';
    flex: 1;
    border-bottom: 1px solid #dbdbdb;
  }

  &::before {
    margin-right: 0.75rem;
  }

  &::after {
    margin-left: 0.75rem;
  }
}
